.preloader {
  --duration: 1s;
  position: fixed;
  z-index: 2000;
  background: #111;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: var(--duration);
}

.preloader__content {
  position: relative;
  width: 200px;
  height: 200px;
}

.preloader__first,
.preloader__second,
.preloader__third {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: var(--duration) infinite linear;
}

.preloader__first {
  border-bottom: 3px solid #042a8c;
  animation-name: first;
}

.preloader__second {
  border-right: 3px solid #ed0677;
  animation-name: second;
}

.preloader__third {
  border-top: 3px solid #14b1e7;
  animation-name: third;
}

@keyframes first {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@keyframes second {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@keyframes third {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Cera Round Pro", "Roboto", sans-serif;
  overflow-x: hidden;
}

button {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  outline: none;
}

input:not([type="range"]),
select,
textarea {
  border: none;
  outline: none;
  transition: 0.3s ease;
}

input:not([type="range"]):focus,
select:focus,
textarea:focus {
  box-shadow: 0 0 0 3px #042a8c;
}

.overflow-hidden {
  overflow: hidden;
}

.even-logo {
  display: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}

.basic-flex {
  display: flex;
  align-items: center;
}

.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.img-wrapper img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.fullpage-menu {
  position: absolute;
  max-width: 1780px;
  width: calc(100% - 140px);
  height: 100vh;
  top: 0;
  right: 0;
  transform: translateY(-150%);
  padding: 45px 40px;
  background-color: #042a8c;
  overflow: hidden;
  z-index: 10;
  opacity: 1;
  pointer-events: none;
  transition: 0.5s ease;
  background-image: url("../img/menu-bg.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top right;
}

.fullpage-menu.active {
  pointer-events: unset;
  transform: translateY(0);
}

.fullpage-menu .bottom-img {
  position: absolute;
  bottom: 0;
  right: 0;
}

.fullpage-menu .circle.primary {
  width: 240px;
  height: 240px;
  top: -150px;
  left: 55%;
}

.fullpage-menu .circle.secondary {
  width: 500px;
  height: 500px;
  bottom: -250px;
  right: -20px;
}

.fullpage-menu .big-text {
  top: 45%;
}

.main-menu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s ease;
}

.main-menu .main-menu__inner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-menu .main-menu__inner:not(:last-child) {
  margin-bottom: 100px;
}

.main-menu li {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-menu li a {
  white-space: nowrap;
  font-size: 36px;
  color: #fff;
  text-transform: uppercase;
  transition: 0.3s ease;
}

.main-menu li a:hover,
.main-menu li a:focus {
  color: #ed0677;
}

.main-menu li::after {
  display: block;
  content: "";
  margin: 0 40px;
  width: 140px;
  height: 3px;
  background-color: #14b1e7;
  transform: rotate(-45deg);
}

.main-menu li:nth-child(4)::after {
  display: none;
}

.main-menu li:last-child {
  margin-right: 0;
}

.main-menu li:last-child::after {
  display: none;
}

.main-menu li.active a {
  color: #ed0677;
}

.menu-toggler-btn {
  display: flex;
  align-items: center;
}

.menu-toggler-btn .img-wrapper {
  margin-right: 15px;
}

.menu-toggler-btn span {
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 900;
}

@media (max-width: 1700px) {
  .fullpage-menu {
    width: 100%;
  }

  .main-menu .main-menu__inner {
    flex-direction: row;
    align-items: center;
  }

  .main-menu li a {
    font-size: 28px;
  }

  .main-menu li::after {
    width: 80px;
    height: 2px;
  }
}

@media (max-width: 968px) {
  .fullpage-menu {
    padding: 25px;
    background-image: none;
    background-color: #042a8c;
  }

  .fullpage-menu .big-text {
    display: none;
  }

  .main-menu {
    position: unset;
    transform: none;
    margin-top: 10vh;
  }

  .main-menu li {
    margin-bottom: 30px;
  }

  .main-menu li a {
    font-size: 20px;
  }

  .main-menu li::after {
    display: none;
  }

  .main-menu .main-menu__inner {
    margin-bottom: 0 !important;
    flex-direction: column;
  }
}